* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul, ol, li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

figure {
  width: 100%;
  height: 100vh;
  background: linear-gradient(45deg, pink, violet);
}
figure h1 {
  position: absolute;
  top: 7vh;
  left: 4vw;
  font-size: 0;
}
figure h1 strong {
  font: 36px/1.4 "arial";
  color: #fff;
  letter-spacing: 1px;
}
figure h1 span {
  font: 12px/1 "arial";
  color: #fff;
  opacity: 0.8;
  letter-spacing: 1px;
}
figure .menu {
  position: absolute;
  top: 8vh;
  right: 4vw;
  font-size: 24px;
  color: #fff;
}
figure > p {
  position: absolute;
  bottom: 7vh;
  left: 50%;
  transform: translateX(-50%);
  font: 12px/1 "arial";
  color: #fff;
  letter-spacing: 2px;
  opacity: 0.8;
}

